    export default {
        name: 'comment',
	    data() {
            return {
	            list: [],
                over: false,
	            page: 1,
	            is_reply: 2,
	            score: 3,
                first: true,
            }
	    },
	    onLoad() { this.$commonLoad.onload();
            this.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            this.$request({
	            url: this.$api.app_admin.comments,
	            data: {
                    score: 3,
                    is_reply: 2,
                    page: 1,
	            }
            }).then(response => {
                this.$hideLoading();
                if (response.code === 0) {
                    this.first = false;
                    this.list = response.data.list;
                }
            })
	    },
        onReachBottom() {
            if (!this.over) {
                this.page++;
                this.request();
            }
        },
        onShow() {
            if(this.first) {
                return false
            }
            this.$request({
                url: this.$api.app_admin.comments,
                data: {
                    score: this.score,
                    is_reply: this.is_reply,
                    page: this.page,
                }
            }).then(response => {
                if (response.code === 0) {
                    for(let i in response.data.list) {
                        for(let j in this.list) {
                            this.list = response.data.list;
                        }
                    }
                }
            })
        },
	    methods: {
            setActiveNav(data) {
                this.page = 1;
                this.score = data;
                this.is_reply = 2;
                this.over = false;
                uni.pageScrollTo({
                    scrollTop: 0
                });
                uni.showLoading({
                    title: '加载中...'
                });
                this.$request({
                    url: this.$api.app_admin.comments,
                    data: {
                        score: this.score,
                        is_reply: this.is_reply,
                        page: this.page,
                    }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.list = response.data.list;
                    }
                })
            },
		    setActiveReply(data) {
                this.page = 1;
                this.is_reply = data;
                this.over = false;
                uni.showLoading({
                    title: '加载中...'
                });
                this.$request({
                    url: this.$api.app_admin.comments,
                    data: {
                        score: this.score,
                        is_reply: this.is_reply,
                        page: this.page,
                    }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.list = response.data.list;
                    }
                });
		    },
		    request() {
                this.$request({
	                url: this.$api.app_admin.comments,
	                data: {
	                    score: this.score,
		                is_reply: this.is_reply,
		                page: this.page,
                    }
                }).then(response => {
                    if (response.code === 0) {
                        if (response.data.list.length > 0) {
                            this.list = [...this.list, ...response.data.list];
                        } else {
                            this.over = true;
                        }
                    }
                })
            },
            isTop(data, index) {
                const self = this;
                const is_top = data.is_top == 1 ? 0 : 1;
                self.$request({
                    url: self.$api.app_admin.comments_top,
                    method: 'POST',
                    data: {
                        status: is_top,
                        id: data.id
                    }
                }).then(info => {
                    if (info.code === 0) {
                        data.is_top = is_top;
                        self.list.splice(index, 1, data);
                        uni.showToast({title: info.msg, icon: 'none'});
                    }
                })
            },

            isShow(data) {
                if (data.is_show === 0) {
                    this.$request({
                        url: this.$api.app_admin.comments_show,
                        method: 'POST',
                        data: {
                            is_show: 1,
                            id: data.id,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            for (let i = 0; i < this.list.length; i++) {
                                if (data.id === this.list[i].id) {
                                    this.list[i].is_show = 1;
                                }
                            }
                        }
                    })
                } else {
                    this.$request({
                        url: this.$api.app_admin.comments_show,
                        method: 'POST',
                        data: {
                            is_show: 0,
                            id: data.id,
                        }
                    }).then(response => {
                        if (response.code === 0) {
                            for (let i = 0; i < this.list.length; i++) {
                                if (data.id === this.list[i].id) {
                                    this.list[i].is_show = 0;
                                }
                            }
                        }
                    })
                }
            }
	    },
    }
